.contacts{
  &__inner{
    padding: 3.75em;
    background-color: $gray;
    border-radius: 1.375em;
  }
  &__info{
    display: flex;
    flex-direction: column;
  }
  &__items{
    margin: 3.125em 0;
    @include grid100gap(2.875em);
  }
  .socials{
    margin-top: auto;
    justify-content: unset;
    &__item{
      width: 3.75em;
      @include lastM(0 1.5em 0 0);
    }
  }
}

.contact-item{
  @include grid100gap(1em);
}

.contacts-form,
.contacts-form__bottom{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .75em));
  gap: 1.5em;
}

.contacts-form{
  align-self: flex-start;
  ._full{
    grid-column: 1/-1;
  }
  &__hint{
    a{
      font-weight: 600;
      color: $green;
    }
  }
  &__bottom{
    margin-top: 1.375em;
  }
}


@media screen and (max-width: 992px){
  .contacts{
    &__inner{
      gap: 2.5em;
      grid-template-columns: 100%;
      padding: 2.5em;
    }
    &__items{
      margin: 2em 0;
      grid-template-columns: repeat(2,calc(50% - 1.5em));
      gap: 1em .75em;
    }
    .socials{
      &__item{
        width: 3.125em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__inner{
      gap: 1.5em;
      padding: 1.5em 1em;
    }
    &__items{
      margin: 1.5em 0;
      grid-template-columns: 100%;
    }
    .socials{
      &__item{
        width: 2.5em;
        @include lastM(0 1em 0 0);
      }
    }
  }

  .contact-item{
    gap: .5em;
    .text15{
      font-size: .8125em;
      line-height: 115%;
    }
    &__hint{
      .text13{
        font-size: .625em;
        line-height: 100%;
      }
    }
  }

  .contacts-form{
    grid-template-columns: 100%;
    gap: 1em;
    &__bottom{
      grid-template-columns: 100%;
      margin-top: .5em;
      gap: .5em;
    }
  }
}
