.footer{
  padding: 6.25em 0 5em;
  border-top: 1px solid rgba(192, 212, 235, 0.50);
  background-color: $gray;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__info{
    width: 18.3em;
    .text{
      margin-top: 1.5em;
      max-width: 17em;
    }
  }
  &__logo{
    width: 100%;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__nav{
    flex-grow: 1;
    max-width: 25em;
    display: flex;
    justify-content: space-between;
  }
  &__title{
    font-weight: 500;
    color: $gray2;
  }
  &__links{
    margin-top: 1.5em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    @include lastM(0 0 1.5em);
    span{
      font-size: .9375em;
      line-height: 133%;
    }
  }
  .socials{
    justify-content: unset;
    margin-top: .5em;
    @media screen and (min-width: 993px){
      flex-wrap: wrap;
      max-width: 11em;
      &__item{
        margin: 1em 1em 0 0 !important;
      }
    }
  }
  &__bottom{
    padding-top: 2.5em;
    margin-top: 2.5em;
    border-top: 2px solid rgba($gray2,.1);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__links2{
    display: flex;
  }
  &__link2{
    @include lastM(0 1.5em 0 0);
    span{
      font-size: .8125em;
      line-height: 139%;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 5em 0 2.5em;
    &__info{
      width: 11.4375em;
      .text{
        margin-top: 1em;
        p{
          font-size: .6875em;
          line-height: 109%;
        }
      }
    }
    &__nav{
      max-width: 100%;
      flex-grow: unset;
    }
    &__group{
      min-width: 9.9375em;
      margin-left: 1.5em;
    }
    .socials{
      margin-top: 1em;
      &__item{
        @include lastM(0 .75em 0 0);
      }
    }
    &__title{
      font-size: 1.125em;
      line-height: 111%;
    }
    &__links{
      margin-top: 1em;
    }
    &__link{
      @include lastM(0 0 1em);
    }
    &__link2,
    &__c{
      span{
        font-size: .6875em;
        line-height: 109%;
      }
    }
    &__link2{
      @include lastM(0 1em 0 0);
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding: 3.5em 0 1.5em;
    &__top{
      display: block;
    }
    &__info{
      width: 13.8125em;
      .text{
        max-width: 12em;
        p{
          font-size: .625em;
          line-height: 100%;
        }
      }
    }
    &__nav{
      display: block;
      margin-top: 2.5em;
    }
    &__group{
      margin: 1.5em 0 0;
    }
    &__title{
      font-size: 1em;
      line-height: 112.5%;
    }
    &__link{
      @include lastM(0 0 .75em);
      span{
        font-size: .8125em;
        line-height: 116%;
      }
    }
    .socials{
      margin-top: 1.5em;
      &__item{
        @include lastM(0 1em 0 0);
      }
    }
    &__bottom{
      flex-direction: column;
      align-items: flex-start;
    }
    &__links2{
      order: -1;
      margin-bottom: 1.5em;
    }
    &__link2,
    &__c{
      span{
        font-size: .625em;
        line-height: 100%;
      }
    }
    &__link2{
      @include lastM(0 .75em 0 0);
    }
  }
}
