.header{
  position: relative;
  z-index: 10;
  padding: 1.375em 0;
  border-bottom: 1px solid #F4F7FB;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    width: 10.625em;
    flex-shrink: 0;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &-action .m-btn{
    padding: 0 2em;
    height: 2.3125em;
  }
}

.header-action{
  display: flex;
  align-items: center;
  margin-left: auto;
}

.header-feedback{
  margin-right: 2em;
  position: relative;
  &__btn{
    display: flex;
    align-items: center;
    color: $black;
    height: 2.3125em;
    svg{
      margin-right: .5em;
      flex-shrink: 0;
      width: 1.5em;
      height: auto;
    }
    &:hover{
      color: $green;
      path{
        stroke: $green;
      }
    }
  }
  &._show{
    .feedback-dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.feedback-dropdown{
  position: absolute;
  top: 99%;
  left: 50%;
  transform: translateX(-50%);
  padding-top: 1.75em;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &__inner{
    border-radius: 1.375em;
    border: 2px solid $gray;
    background: #fff;
    box-shadow: 0 1.875em 3.75em 0 rgba(120, 143, 190, 0.05);
  }
  &__top{
    padding: 1.5em;
  }
  &__bottom{
    border-top: 2px solid $gray;
    padding: 1.5em;
    .m-btn{
      white-space: nowrap;
    }
  }
}

.socials{
  display: flex;
  justify-content: center;
  align-items: center;
  &__item{
    width: 2.5em;
    flex-shrink: 0;
    @include lastM(0 1em 0 0);
    &:hover{
      img{
        transform: translateY(-25%);
      }
    }
    img{
      width: 100%;
      display: block;
      transition: inherit;
    }
  }
}

.burger{
  display: none;
}

.menu{
  margin-left: 2.44em;
  &__bottom{
    display: none;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    @include lastM(0 2em 0 0);
    span{
      font-size: .9375em;
      line-height: 133%;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__inner{
      justify-content: flex-end;
    }
    &__logo{
      margin-right: auto;
      position: relative;
      z-index: 21;
    }
    &._open{
      .menu{
        transform: translateX(0);
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .burger{
    z-index: 21;
    width: 2.125em;
    height: 2.125em;
    background-color: $blue;
    border-radius: .75em;
    display: flex;
    position: relative;
    flex-shrink: 0;
    margin-left: 1.5em;
    img{
      @include centerAbsolute;
      width: 100%;
    }
    ._view2{
      opacity: 0;
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    height: $vh;
    margin: 0;
    width: 100%;
    background-color: #FFF;
    background-image: url(../img/menu-bg.svg);
    background-repeat: no-repeat;
    background-position: right bottom;
    background-size: 25.6875em auto;
    overflow-y: auto;
    z-index: 20;
    padding: 10em 1em 5em;
    transition: all .5s;
    transform: translateX(-120%);
    display: flex;
    flex-direction: column;
    align-items: center;
    &__bottom{
      padding-top: 2em;
      margin-top: auto;
      display: flex;
      flex-direction: column;
      align-items: center;
      .m-btn{

      }
      .socials{
        margin-top: 2em;
      }
    }
  }

  .nav{
    flex-direction: column;
    &__link{
      font-weight: 600;
      @include lastM(0 0 2.5em);
      span{
        font-size: 1.375em;
        line-height: 118%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    padding: .75em 0;
    &__logo{
      width: 7.6875em;
    }
  }

  .header-action{
    &__btn{
      padding: 0 1em;
      height: 2.125em;
      span{
        font-size: .875em;
      }
    }
  }

  .header-feedback{
    display: none;
  }

  .burger{
    margin-left: 1em;
  }

  .menu{
    background-size: 12.125em auto;
    padding-top: 9em;
    padding-bottom: 1.5em;
    .socials{
      margin-top: 1.5em;
    }
  }

  .nav{
    &__link{
      @include lastM(0 0 1.5em);
      span{
        font-size: 1em;
        line-height: 112%;
      }
    }
  }
}
