.results{
  &__top{
    max-width: 38.625em;
  }
  &__stat2{
    margin-top: 3.125em;
    .text46{
      font-weight: 700;
    }
  }
}

.how-work{
  &__text{
    margin-top: 1.5em;
  }
}

.how-work2{
  background-image: unset;
}

.guide{
  padding: 2.5em;
  border-radius: 1.375em;
  background-color: $gray;
  margin-top: 3.125em;
}

.guide-slider{
  margin: 0;
  overflow: hidden;
  .swiper{
    &-slide{
      width: 100%;
    }
    &-nav{
      position: absolute;
      bottom: 0;
      left: 50%;
      margin-left: 1.4375em;
      display: flex;
    }
    &-button{
      position: static;
      transform: unset;
      margin: 0 1.5em 0 0;
    }
  }
}

.guide-card{
  &__img{
    img{
      border: 1px solid #C0D4EB;
      width: 100%;
      display: block;
      border-radius: 1.375em;
    }
  }
  &__content{
    padding-bottom: 4em;
  }
  &__top{
    @include grid100gap(.5em);
  }
  .text{
    margin-top: 1.5em;
  }
}

.main {
  &._type2 {
    .main__content{
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }
}


@media screen and (max-width: 992px){
  .results{
    &__stat2{
      margin-top: 2.5em;
      grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
      gap: 1.5em;
      @media screen and (min-width: 577px){
        .text46{
          font-size: 2em;
        }
        .text13{
          font-size: .9375em;
          line-height: 133%;
        }
      }
    }
  }

  .guide{
    margin-top: 2.5em;
    padding: 1.5em;
    overflow: hidden;
  }

  .guide-slider{
    overflow: unset;
    .swiper{
      &-container{
        width: calc(100% + 2em);
      }
      &-slide{
        padding-right: 2em;
      }
      &-nav{
        margin: 1.5em 0 0;
        position: static;
        justify-content: unset;
      }
      &-button{
        margin-right: 1.5em;
      }
    }
  }

  .guide-card{
    &__grid{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
    &__content{
      padding: 0;
    }
    .text{
      margin-top: 1em;
    }
    .text20{
      font-size: 1.125em;
      line-height: 111%;
    }
  }

  .main{
    &._type2{
      @media screen and (min-width: 577px){
        .main__img{
          position: absolute;
          top: 0;
          right: 0;
          height: 100%;
          img{
            height: 100%;
            width: auto;
          }
        }
        .text52{
          width: 12.5em;
        }
        .main__nav{
          .m-btn{
            width: auto;
          }
        }
      }
    }
  }
}



@media screen and (max-width: 576px){
  .results{
    &__stat2 {
      margin-top: 2em;
      grid-template-columns: repeat(2, calc(50% - .5em));
      gap: 1em;
    }
  }

  .guide{
    margin-top: 2em;
    padding: 1.5em 1em;
  }

  .guide-slider{
    .swiper-nav{
      justify-content: center;
    }
    .swiper-button{
      margin: 0 .75em;
    }
  }

  .guide-card{
    &__top{
      gap: .375em;
    }
    .text20{
      font-size: 1em;
      line-height: 112.5%;
    }
  }
  .main {
    &._type2 {
      .main__img{
        ._view2{
          display: none;
        }
        ._view3{
          display: block;
        }
      }
    }
  }
}
