.fg {
  position: relative;
  margin: 0;
  input,
  textarea{
    border: 2px solid #CBD2DF;
    padding: 0 1.5em;
    height: 3.75em;
    width: 100%;
    border-radius: 1.875em;
    font-size: 1em;
    line-height: 125%;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: $gray2;
    }
    &:focus {
      border-color: $primary;
    }
  }
  textarea{
    padding-top: 1.44em;
    padding-bottom: 1.44em;
    height: 9.1875em;
    resize: none;
  }
}


@media screen and (max-width: 576px){
  .fg{
    textarea,
    input{
      border-radius: 1.85em;
      font-size: .8125em;
      height: 3.53em;
      padding: 0 1.23em;
    }
    textarea{
      height: 9.15em;
      padding: 1.23em;
    }
  }
}
