.tariffs{
  overflow: hidden;
  background-color: $gray;
  &__text{
    margin-top: 1.5em;
  }
}

.tariffs-slider{
  .swiper{
    &-nav{
      display: none;
    }
  }
}

.tariff-card{
  padding: 2em;
  border-radius: 1.375em;
  background-color: #fff;
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  &._blue{
    .tariff-card__badge{
      background-color: #C0F0FF;
    }
    .tariff-group path{
      stroke: #01BAF5;
    }
  }
  &._green{
    .tariff-card__badge{
      background-color: #EFF7D9;
    }
    .tariff-group path{
      stroke: #80AC06;
    }
  }
  &__badge{
    position: absolute;
    top: 0;
    right: 2.44em;
    transform: translateY(-50%);
    padding: 0 1.12em;
    height: 2.375em;
    display: flex;
    align-items: center;
    border-radius: 6.1875em;
    background: #FFF0B4;
  }
  &__groups{
    margin: 2em 0;
    @include grid100gap(1.5em);
  }
  &__bottom{
    margin-top: auto;
    padding-top: 2em;
    border-top: 1px dashed rgba(#C0D4EB,.4);
    .m-btn{
      width: 100%;
      margin-top: 1.5em;
    }
  }
  &__prices{
    display: flex;
    align-items: center;
    img{
      margin: 0 1em;
      width: .8125em;
      flex-shrink: 0;
    }
  }
}

.tariff-group{
  display: flex;
  align-items: flex-start;
  svg{
    width: 1.25em;
    height: auto;
    flex-shrink: 0;
    margin-right: .5em;
  }
  &__items{
    margin-top: .5em;
    color: $gray2;
    @include grid100gap(.625em);
  }
}

.faq{
  &__inner{
    display: grid;
    grid-template-columns: 31.75em calc(100% - 31.75em - 2.94em);
    gap: 2.94em;
  }
  &__left{
    .text{
      margin-top: 1.5em;
      max-width: 27em;
    }
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
  .socials{
    margin-left: 1.75em;
    &__item{
      width: 3.75em;
      @include lastM(0);
      &:not(:first-child){
        margin-left: -.5em;
      }
    }
  }
  &__items{
    @include grid100gap(1.5em);
  }
}

.qa-card{
  border-radius: 1.375em;
  background-color: $gray;
  &._active{
    .qa-card__btn img{
      transform: rotate(-180deg);
    }
  }
  &__show{
    cursor: pointer;
    padding: 1.5em 2.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    width: 2.5em;
    height: 2.5em;
    flex-shrink: 0;
    margin-left: 1em;
    border-radius: 1em;
    background-color: $green;
    @include center;
    img{
      width: 1.125em;
      display: block;
      transition: all .3s;
    }
  }
  &__hidden{
    padding: 0 2.5em 1.5em;
    display: none;
  }
}

.reviews{
  background-color: $gray;
  overflow: hidden;
}

.reviews-slider{
  @media screen and (min-width: 993px){
    .swiper-container{
      overflow: hidden;
    }
  }
}

.review-card{
  display: block;
  &__img{
    border: 2px solid $gray;
    border-radius: 1.375em;
    @include imgCover(115.5%);
  }
}

.offer{
  position: relative;
  background: linear-gradient(99deg, #65D9FF -50.14%, #75B4FF 149.75%);
  &__bg{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 26.875em;
  }
  &__inner{
    position: relative;
    z-index: 2;
    padding: 3.75em;
    border-radius: 1.375em;
    background-color: #fff;
    display: grid;
    grid-template-columns: calc(100% - 28.0625em - 2.5em) 28.0625em;
    gap: 2.5em;
    align-items: flex-start;
  }
  &__nav{
    @include grid100gap(1.5em);
  }
  .m-badge{
    margin-bottom: 1em;
    display: inline-flex;
  }
}


@media screen and (max-width: 992px){
  .tariffs{
    &__text{
      margin-top: 1em;
    }
  }

  .tariffs-slider{
    .swiper{
      &-container{
        width: calc(100% + 1.5em);
      }
      &-slide{
        width: 50%;
      }
      &-nav{
        display: flex;
      }
    }
  }

  .tariff-card{
    padding: 1.5em;
    &__badge{
      right: 1.5em;
      height: 1.75em;
      padding: 0 .75em;
    }
    &__groups{
      margin: 1.5em 0;
      gap: 1em;
    }
    &__bottom{
      padding-top: 1.5em;
    }
    &__prices{
      img{
        margin: 0 .4375em;
        width: .6875em;
      }
    }
  }

  .tariff-group{
    &__items{
      gap: .5em;
    }
  }

  .faq{
    &__inner{
      grid-template-columns: 100%;
      gap: 2.5em;
    }
    &__left{
      .text{
        margin-top: 1em;
        max-width: 95%;
      }
    }
    .socials{
      &__item{
        width: 3.125em;
        &:not(:first-child){
          margin-left: .75em;
        }
      }
    }
  }

  .qa-card{
    &__show{
      padding: 1em 1.5em;
      .text20{
        font-size: 1.125em;
      }
    }
    &__hidden{
      padding: 0 1.5em 1em;
    }
    &__btn{
      width: 2.125em;
      height: 2.125em;
      border-radius: .75em;
      img{
        width: 1em;
      }
    }
  }

  .reviews-slider{
    .swiper-pagination{
      display: none;
    }
  }

  .offer{
    &__bg{
      width: 18.5em;
    }
    &__inner{
      padding: 2.5em;
      grid-template-columns: 100%;
    }
    &__content{
      max-width: 37em;
    }
  }
}


@media screen and (max-width: 576px){
  .tariffs-slider{
    margin-top: 2em;
    .swiper{
      &-container{
        width: 100%;
      }
      &-wrapper{
        transform: unset !important;
        @include grid100gap(1.75em);
      }
      &-slide{
        width: 100%;
        padding: 0;
      }
      &-nav{
        display: none;
      }
    }
  }

  .tariff-card{
    &__prices{
      img{
        width: .5em;
      }
    }
  }

  .qa-card{
    &__show{
      padding: .75em 1em;
      .text20{
        font-size: 1em;
      }
    }
    &__hidden{
      padding: 0 1em 1em;
    }
  }

  .faq{
    &__nav{
      margin-top: 1.5em;
      display: block;
      .m-btn{
        width: 100%;
      }
    }
    .socials{
      margin: 1.5em 0 0;
      justify-content: unset;
      &__item{
        width: 2.5em;
        &:not(:first-child){
          margin-left: 1em;
        }
      }
    }
  }

  .offer{
    &__bg{
      width: 12.5em;
    }
    &__inner{
      padding: 1.5em;
      gap: 2em;
    }
    &__nav{
      gap: 1em;
    }
    .m-badge{
      padding: .5em .75em;
      height: unset;
      min-height: 2.375em;
      span{
        line-height: 100%;
      }
    }
  }
}
