.text{
  color: $gray2;
  p,li{
    font-size: .9375em;
    line-height: 133%;
  }
  p{
    @include lastM(0 0 1.07em);
  }
  ol,ul{
    @include grid100gap(.75em);
    @include lastM(0 0 1em);
  }
  ul{
    li{
      position: relative;
      padding-left: 1.53em;
      &:before{
        content: 'â€”';
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
  ol{
    counter-reset: nums;
    li{
      display: flex;
      counter-increment: nums;
      &:before{
        content: counter(nums) '. ';
        padding-right: .4em;
        @include sameNumWidth;
        min-width: .93em;
      }
    }
  }
  b{
    font-weight: 600;
    color: $black;
  }
}


@media screen and (max-width: 992px){
  .text{
    p{
      @include lastM(0 0 .8em);
    }
  }
}


@media screen and (max-width: 576px){
  .text{
    p,li{
      font-size: .8125em;
      line-height: 115%;
    }
    p{
      @include lastM(0 0 .92em);
    }
    ul{
      li{
        padding-left: 1.61em;
      }
    }
  }
}
