@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }

    height: 3.75em;
    padding: 0 2.75em;
    img{
        width: 1.5em;
        margin-right: .75em;
        flex-shrink: 0;
    }
    span{
        font-size: 1.125em;
    }
}

.m-btn-primary-outline{

    border-radius: 6.1875em;
    border: 2px solid #FFC304;
    font-weight: 500;
    color: $black;
    &:hover{
        background-color: #FFC304;
        color: $black;
    }
    span{
        font-size: 1.125em;
        line-height: 111%;
    }
}

.m-btn-primary{
    border-radius: 62.4375em;
    border: 0.5px solid $primary;
    background: linear-gradient(332deg, rgba(255, 255, 255, 0.00) 30.12%, rgba(255, 255, 255, 0.20) 117.35%), linear-gradient(127deg, #FED842 7.05%, #F0C10D 103.31%);
    box-shadow: 0px 5px 20px 0px rgba(255, 192, 97, 0.26), 0px 1px 10px 0px #FFD943 inset, 0px 1px 0.5px 0px rgba(255, 255, 255, 0.78) inset;
    position: relative;
    overflow: hidden;
    color: $black;
    &:after{
        content: '';
        position: absolute;
        top: 0;
        background-image: url(../img/btn-line.svg);
        width: 100%;
        height: 100%;
        right: 0;
        background-size: auto 100%;
        background-repeat: no-repeat;
        background-position: right top;
        transition: all .3s;
    }
    &:hover{
        box-shadow: 2px 5px 30px 15px rgba(255, 192, 97, 0.26), 0px 1px 10px 0px #FFD943 inset, 0px 1px 0.5px 0px rgba(255, 255, 255, 0.78) inset;
        background-size: 200% auto;
        color: $black;
        &:after{
            transform: scale(1.5) translateX(-40%);
        }
    }
    span{
        position: relative;
        z-index: 2;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        padding: 0 2.37em;
        height: 3.125em;
        img{
            width: 1.125em;
            margin-right: .625em;
        }
        span{
            font-size: 1em;
            line-height: 120%;
        }
    }
}


@media screen and (max-width: 576px){
    .m-btn{
        height: 2.875em;
        span{
            font-size: .875em;
        }
    }
}
