.grid-column2,
.grid-column4{
  display: grid;
  gap: 2.875em;
}

.grid-column2{
  grid-template-columns: repeat(2,calc(50% - 2.875em / 2));
}

.grid-column4{
  grid-template-columns: repeat(4,calc(25% - 2.875em * 3 / 4));
}


@media screen and (max-width: 992px){
  .grid-column2-tablet{
    gap: 1.5em;
  }

  .grid-column2-tablet{
    grid-template-columns: repeat(2,calc(50% - .75em));
  }

  .grid-full-tablet{
    grid-template-columns: 100%;
  }
}


@media screen and (max-width: 576px){
  .grid-full-mob{
    gap: 1.5em;
  }

  .grid-full-mob{
    grid-template-columns: 100%;
  }
}
