.main {
  &._small {
    .main__inner{
      min-height: 43.75em;
    }
  }
}

.video-consult{
  &__info{
    margin-top: 3.125em;
    display: inline-flex;
    padding: .375em;
    border-radius: 6.1875em;
    border: 2px solid $gray2;
    .tariff-card__prices{
      padding: 0 1.5em 0 1.625em;
    }
  }
  &._reverse{
    .how-help__media{
      order: -1;
    }
  }
}

._gray-bg{
  background-color: $gray;
}

._white-bg{
  background-color: transparent;
}


@media screen and (max-width: 992px){
  .main {
    &._small {
      .main__inner{
        min-height: 29.125em;
      }
    }
  }

  .video-consult{
    .how-help__media{
      order: -1;
    }
    &__info{
      margin-top: 2.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .main {
    &._small {
      .main__inner{
        min-height: unset;
      }
      .main__img{
        margin: -13em 0 0;
      }
    }
  }

  .video-consult{
    &__info{
      display: block;
      padding: 0;
      border: none;
      .tariff-card__prices{
        padding: 0;
      }
      .m-btn{
        width: 100%;
        margin: 1em 0 0;
      }
    }
  }
}
