.main{
  background-color: $gray;
  position: relative;
  overflow: hidden;
  &__inner{
    padding: 10.5em 0;
    min-height: 48.75em;
    display: grid;
    grid-template-columns: 45.6875em calc(100% - 46.6875em);
    gap: 1em;
  }
  &__content{
    position: relative;
    z-index: 2;
  }
  &__text{
    max-width: 38.6875em;
    margin-top: 2em;
    b{
      font-weight: 600;
      color: $black;
    }
  }
  &__nav{
    display: flex;
    margin-top: 3.125em;
    .m-btn{
      @include lastM(0 1.5em 0 0);
    }
  }
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    img{
      height: 100%;
      display: block;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  .m-badge{
    margin-bottom: 1em;
    display: inline-flex;
  }
}

.m-badge{
  padding: 0 1.12em;
  height: 2.375em;
  display: flex;
  align-items: center;
  border-radius: 6.1875em;
  background-color: #EFF7D9;
  img{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .5em;
  }
}

._section{
  padding: 10em 0;
}

.how-work,
.for-who{
  &__items{
    margin-top: 3.125em;
  }
}

.who-card{
  padding: 1.5em;
  border-radius: 1.375em;
  background-color: $gray;
  &__top{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 2.5em;
    flex-shrink: 0;
    margin-right: 1em;
    img{
      width: 100%;
      display: block;
    }
  }
  .text{
    margin-top: 1.5em;
  }
}

.how-help{
  background-color: $gray;
  ._center{
    align-items: center;
  }
  .text{
    margin-top: 1.5em;
  }
  &__nav{
    margin-top: 3.125em;
    display: flex;
  }
}

.video{
  position: relative;
  display: block;
  &__play{
    @include centerAbsolute;
    width: 3.75em;
    height: 3.75em;
    padding: 0;
    border-radius: 50%;
    box-shadow: 0px 10px 30px 0px rgba(249, 210, 59, 0.25);
    img{
      margin: 0;
    }
  }
  &__img{
    display: block;
    width: 100%;
    border-radius: 1.375em;
  }
}

.how-work{
  position: relative;
  background-image: url(../img/lines.svg);
  background-repeat: no-repeat;
  background-position: center top 15em;
  background-size: 42em auto;
  &__line{
    position: absolute;
    width: 4.6em;
    bottom: 18.06em;
    left: 50%;
    margin-left: -1.42em;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}

.how-card{
  padding: 2em;
  background-color: $gray;
  border-radius: 1.375em;
  &__top{
    display: flex;
    align-items: center;
  }
  &__num{
    @include center;
    flex-shrink: 0;
    margin-right: 1em;
    border-radius: 50%;
    width: 2.5em;
    height: 2.5em;
    color: #fff;
    background-color: $green;
  }
  .text{
    margin-top: 1.5em;
  }
}

.results{
  background-color: $gray;
  position: relative;
  &__bg{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 26.875em;
  }
  .container{
    position: relative;
    z-index: 2;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 1.375em;
    }
  }
  &__content{
    display: flex;
    flex-direction: column;
  }
  .text{
    margin-top: 1.5em;
    margin-bottom: 2.5em;
  }
  &__stat{
    margin-top: auto;
    padding-top: 2.5em;
    border-top: 1px solid rgba($gray2,.1);
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 6.25em / 3));
    gap: 3.125em;
    align-items: flex-start;
  }
}

.stat-item{
  @include grid100gap(.75em);
}

._slider{
  margin-top: 3.125em;
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 2.875em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      padding-right: 2.875em;
      width: calc(100% / 3);
      height: auto;
    }
    &-button{
      width: 3.75em;
      height: 3.75em;
      border-radius: 50%;
      background-color: #FFCB03;
      z-index: 2;
      transition: all .3s;
      &:hover{
        background-color: darken(#FFCB03,5%);
      }
      svg{
        width: 100%;
        height: auto;
      }
      &-prev{
        left: -5.75em;
      }
      &-next{
        right: -5.75em;
      }
      &.swiper-button-disabled{
        background-color: #fff;
        opacity: 1;
        path{
          stroke: #CBD2DF;
        }
      }
    }
  }
}

.clients{
  overflow: hidden;
}

.clients-slider{
  .swiper{
    &-slide{
      position: relative;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(270deg, #FFF -0.32%, rgba(255, 255, 255, 0.00) 111.31%);
        z-index: 2;
        pointer-events: none;
        transition: all .3s;
      }
      @media screen and (min-width: 993px){
        &-active,
        &-next,
        &-next+.swiper-slide{
          &:before{
            opacity: 0;
          }
        }
      }
    }
  }
}

.client-card{
  position: relative;
  &__img{
    border-radius: 1.125em;
    @include imgCover(145.7%);
  }
  &__content{
    position: absolute;
    width: calc(100% - 3em);
    left: 50%;
    transform: translateX(-50%);
    bottom: 1.5em;
    border-radius: 1.125em;
    background: #FFF;
    backdrop-filter: blur(5px);
    padding: 1.5em;
  }
  &__info{
    @include grid100gap(.75em);
  }
  .m-badge{
    margin-top: 1.5em;
  }
}


@media screen and (max-width: 992px){
  .main{
    &__inner{
      width: calc(100% + 1.25em);
      padding: 1.875em 0 5em;
      min-height: 37.625em;
      grid-template-columns: 21.3125em calc(100% - 22.3125em);
    }
    &__content{
      padding-top: 3.125em;
    }
    &__img{
      position: static;
      height: auto;
      img{
        width: 100%;
        height: auto;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    .m-badge{
      margin-bottom: .75em;
    }
    &__text{
      margin-top: 1.5em;
      max-width: 21em;
    }
    &__nav{
      margin-top: 2.5em;
      display: block;
      .m-btn{
        width: 100%;
        @include lastM(0 0 1.5em);
      }
    }
  }

  .m-badge{
    padding: 0 .75em;
    height: 1.75em;
    span{
      font-size: .6875em;
    }
    img{
      width: 1.125em;
    }
  }

  ._section{
    padding: 5em 0;
  }

  .for-who{
    &__items{
      margin-top: 2.5em;
    }
  }

  .who-card{
    padding: 1em;
    &__icon{
      width: 2.125em;
      margin-right: .75em;
    }
    .text20{
      font-size: 1.125em;
    }
    .text{
      margin-top: 1em;
    }
  }

  .how-help{
    &__inner{
      row-gap: 2.5em;
    }
    .text{
      margin-top: 1em;
    }
    &__nav{
      margin-top: 2.5em;
    }
  }

  .video{
    &__img{
      @media screen and (min-width: 577px){
        border-radius: 1.57em;
      }
    }
  }

  .how-work{
    background-image: url(../img/lines2.svg);
    background-position: center top 8.15em;
    background-size: 26em auto;
    &__items{
      margin-top: 2.5em;
    }
    &__line{
      width: 2.1875em;
      bottom: 12.47em;
      margin-left: -.75em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .how-card{
    padding: 1em;
    &__icon{
      width: 2.125em;
      height: 2.125em;
    }
    .text{
      margin-top: 1em;
    }
  }

  .results{
    &__bg{
      width: 18.5em;
    }
    &__inner{
      row-gap: 2.5em;
    }
    .text{
      margin-top: 1em;
    }
    &__stat{
      grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
      gap: 2.5em;
    }
  }

  .stat-item{
    gap: .5em;
  }

  ._slider{
    margin-top: 2.5em;
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        padding-right: 1.5em;
        width: 17.25em;
      }
      &-nav{
        display: flex;
        justify-content: center;
        margin-top: 1.5em;
      }
      &-button{
        position: static;
        margin: 0 .75em;
        width: 2.5em;
        height: 2.5em;
      }
    }
  }

  .clients-slider{
    .swiper{
      &-slide{
        @media screen and (min-width: 577px){
          &-active,
          &-next,{
            &:before{
              opacity: 0;
            }
          }
        }
      }
    }
  }

  .client-card{
    &__content{
      padding: 1em;
      bottom: .75em;
      width: calc(100% - 1.5em);
      min-height: 8.3125em;
      display: flex;
      flex-direction: column;
    }
    &__info{
      gap: .375em;
    }
    .m-badge{
      margin-top: auto;
      padding: .5em;
      height: unset;
      min-height: 2.125em;
      span{
        line-height: 109%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__inner{
      width: 100%;
      padding: 2.5em 0 0;
      min-height: unset;
      grid-template-columns: 100%;
      gap: 2.5em;
    }
    &__content{
      padding: 0;
    }
    &__text{
      margin-top: 1em;
    }
    &__nav{
      margin-top: 2em;
      .m-btn{
        @include lastM(0 0 1em);
      }
    }
    &__img{
      margin-bottom: -3em;
    }
  }

  ._section{
    padding: 2.5em 0;
  }

  .for-who{
    &__items{
      margin-top: 2em;
    }
  }

  .who-card{
    .text20{
      font-size: 1em;
    }
  }

  .how-help{
    &__nav{
      margin-top: 2em;
      .m-btn{
        width: 100%;
      }
    }
  }

  .how-work{
    background-image: unset;
    &__items{
      margin-top: 2em;
    }
    &__line{
      display: none;
    }
  }

  .results{
    &__bg{
      width: 12.5em;
    }
    .text{
      margin-bottom: 2em;
    }
    &__stat{
      padding-top: 2em;
      grid-template-columns: repeat(3,calc(100% / 3 - .5em / 3));
      gap: .5em;
    }
  }

  .stat-item{
    .text13{
      line-height: 100%;
    }
  }

  ._slider{
    margin-top: 2em;
    .swiper{
      &-slide{
        padding-right: 1em;
        width: 14.815em;
      }
    }
  }

  .clients-slider{
    .swiper{
      &-slide{
        &-active{
          &:before{
            opacity: 0;
          }
        }
      }
    }
  }

  .client-card{
    &__img{
      overflow: hidden;
    }
    &__content{
      border-right: 1px solid $gray;
      border-bottom: 1px solid $gray;
      border-left: 1px solid $gray;
      bottom: 0;
      width: calc(100% + 4px);
      min-height: 7.375em;
    }
    .m-badge{
      margin-top: 1em;
    }
  }
}
